.item {
  display: flex;
  align-items: center;
  width: 400px;
  padding: 20px;
  margin-right: auto;
  margin-left: auto;

  box-shadow: 3px 3px 5px 2px rgba(34, 60, 80, 0.1);
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.status {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-right: 15px;
}

.avatar {
  width: 97px;
  height: 97px;
  border-radius: 5px;
  margin-right: 15px;
}

.name {
  font-size: 32px;
  font-weight: 800;
}
